@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Vollkorn&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.landing-container{height:100vh;margin:0;background-color:#f2e2e4}.landing-container .top-container,.landing-container .bottom-container{background-color:#b53838}.landing-container .top-container{height:auto;border-radius:0 0 5% 5%;display:flex;justify-content:center;padding-bottom:1em}.landing-container .top-container .hero-text{font-size:1.5em;padding:1.5em 1.5em 0 1.5em;line-height:1.5em;font-family:Roboto}.landing-container .top-container .logo-block{font-size:3em;font-weight:bold;font-family:"Bebas Neue",cursive;word-spacing:1em}.landing-container .top-container .hero-text,.landing-container .top-container .logo-block{font-size:1.5em;color:#fff}.landing-container .middle-container{height:30%;display:flex;align-items:center}.landing-container .middle-container>div{width:100vw}.landing-container .middle-container .action-button{width:70vw !important;max-width:300px;min-width:200px;margin-bottom:1em;color:#b53838}.landing-container .bottom-container{height:auto;border-radius:5% 5% 0 0}.landing-container .bottom-container .hero-text{font-size:1.5em;padding:1.5em 1.5em 0 1.5em;line-height:1.5em;font-family:Roboto;color:#fff}
.register-donor-container{height:100vh;margin:0;background-color:#f2e2e4;display:flex;justify-content:center;padding:1em}.register-donor-container .header-text{padding:1em 1em 0 1em;font-family:"Vollkorn",serif;color:#b53838;font-size:larger;display:flex;justify-content:center;text-align:center;font-weight:bold}.register-donor-container .register-donor-form{padding:1em;width:100vw}.register-donor-container .register-donor-form>*{width:80%;max-width:600px;min-width:200px;margin-bottom:2em;padding:1em;border-radius:10px;border:1px solid #b53838;background-color:#fff;font-family:"Vollkorn",serif}.register-donor-container .register-donor-form>*::-webkit-input-placeholder{color:#b16666}.register-donor-container .register-donor-form>*:-ms-input-placeholder{color:#b16666}.register-donor-container .register-donor-form>*::placeholder{color:#b16666}.register-donor-container .register-donor-form>select{color:#b53838}.register-donor-container .register-donor-form #place-search-input{padding:.5em;margin:auto;background-color:#fff}.register-donor-container .register-donor-form .action-button{margin-top:2em}
